//global style
.main-wrapper {
	width: 100%;
	overflow: hidden;
}

body {
	font-family: "Montserrat", sans-serif;

	.main-wrapper {
		background-color: $dark_color;
		width: 100%;
	}
}

.sbsi7l-0.eARkMz {
	display: none;
}

//global style

//header 
header {
	padding: 20px 0;

	.content {
		display: flex;
		justify-content: space-between;
		align-items: center;

		.logo {
			max-width: 200px;
		}

		.links {
			a {
				color: #fff;
				text-transform: capitalize;
				margin: 0 5px;
			}
		}

		.social-media {
			a {
				color: #fff;
				font-size: 20px;
				margin: 0 5px;
			}
		}
	}
}

//header 

//main section
section.hero {
	padding: 150px 0 80px;

	.content {
		text-align: center;

		.logo {
			margin-bottom: 75px;

			img {
				max-width: 500px;
			}
		}

		.button {
			.btn {
				@extend %btn_selector;
			}
		}
	}
}

//main section

//what can
section.what-can {
	padding: $padding_section;

	.main-title {
		text-align: center;
		margin-bottom: 4rem;

		h2 {
			color: #fff;
			font-size: 26px;

			span {
				text-transform: lowercase;
				color: $main_color;
				font-size: 50px;
				font-weight: 700;
				margin: 0 5px;
			}
		}
	}

	.content {
		display: flex;
		justify-content: space-between;

		.box {
			width: calc((100% / 3) - 50px);
			text-align: center;
			padding: 30px;
			background-color: #fff;

			.box-icon {
				margin-bottom: 25px;

				img {
					height: 100px;
					width: 140px;
					object-fit: content;
				}
			}

			.box-title {
				color: $main_color;
				text-transform: uppercase;
			}
		}
	}

	.works-title {
		text-align: center;
		margin-top: 10rem;

		h2 {
			color: #fff;
			text-transform: uppercase;
		}
	}
}

//what can

//works
section.works {
	padding: $padding_section;
	background-color: #fff;

	.content {
		.box {
			position: relative;
			margin-bottom: 25px;

			&::after {
				content: "";
				position: absolute;
				left: 0;
				top: 0;
				width: 100%;
				height: 100%;
				background-image: linear-gradient(to top, rgba(0, 0, 0, 0.5), transparent);
				z-index: 0;
			}

			.box-image {
				img {
					height: 200px;
					object-fit: cover;
					width: 100%;
				}
			}

			.box-title {
				position: absolute;
				bottom: 0;
				left: 0;
				padding-inline-start: 10px;
				z-index: 1;

				h3 {
					display: flex;
					align-items: center;
					color: #fff;
					text-transform: uppercase;
					font-size: 16px;
				}

				img {
					width: 17px;
					margin-inline-start: 10px;
				}
			}
		}

		.button {
			text-align: center;
			padding-top: 30px;

			.btn {
				@extend %btn_selector;
			}
		}
	}
}

//works

//about me
section.about-me {
	padding: $padding_section;

	.main-title {
		text-align: center;
		color: #fff;
		margin-bottom: 7rem;

		h2 {
			opacity: 0.6;
			font-weight: 700;
		}

		p {
			max-width: 550px;
			margin: auto;
			font-size: 14px;
		}
	}

	.content {
		.box {
			margin-bottom: 25px;
			text-align: center;

			.box-icon {
				margin-bottom: 25px;

				img {
					height: 200px;
					object-fit: contain;
					width: 100%;
				}
			}

			.box-title {
				h3 {
					color: #fff;
					text-transform: uppercase;
					font-size: 36px;
					font-weight: 600;

					span {
						color: $main_color;
					}
				}
			}
		}
	}
}

//about me

//call to action
section.cta {
	margin-bottom: 7rem;

	.content {
		background-color: #474747;
		padding: 25px;
		text-align: center;

		.main-title {
			img {
				width: 70px;
				margin-bottom: 10px;
			}

			h2 {
				color: #fff;
				font-weight: 700;
				font-size: 40px;
			}

			p {
				color: #fff;
				opacity: 0.5;
				font-size: 18px;
				text-transform: uppercase;
			}
		}

		.button {
			text-align: center;
			padding-top: 30px;

			.btn {
				@extend %btn_selector;
			}
		}
	}
}

//call to action

//footer
footer {
	padding: 50px 0 30px;
	background-color: #fff;

	.content {
		text-align: center;

		.footer-logo {
			img {
				margin-bottom: 30px;
			}

			p {
				font-size: 14px;
			}
		}

		.footer-links {
			margin-bottom: 25px;

			a {
				color: $main_color;
				text-transform: capitalize;
				margin: 0 5px;
			}
		}

		.social-media {
			margin-bottom: 25px;

			a {
				color: $dark_color;
				font-size: 20px;
				margin: 0 5px;
			}
		}

		.copyrights {
			text-align: center;

			p {
				margin-bottom: 0;
				color: $main_color;
			}
		}
	}
}


//footer