//global variables 
$main_color: #FF0000;
$dark_color: #1A1A1A;
$gray_color: #171717;
$padding_section: 80px 0;
//global styles

//placeholder selectors
%center {
	display: flex;
	align-items: center;
	justify-content: center;
}

%overflow_text {
	overflow: hidden;
	text-overflow: ellipsis;
	display: -webkit-box;
	-webkit-box-orient: vertical;
}

%overlay {
	content: "";
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
}

%before {
	content: "";
	position: absolute;
}

%fitimage {
	width: 100%;
	height: 100%;
	-o-object-fit: cover;
	object-fit: cover;
}

%max_lines {
	overflow: hidden;
	text-overflow: ellipsis;
	-webkit-box-orient: vertical;
	display: -webkit-box;
}

%max_one_line {
	max-width: 100%;
	text-overflow: ellipsis;
	white-space: nowrap;
	overflow: hidden;
}

%btn_selector {
	background-color: $main_color;
	color: #fff;
	text-transform: capitalize;
	border-radius: 35px;
	padding: 7px 35px;
	transition: all ease 0.3s;
	
	&:hover {
		// background-color: $dark_color;
	}
}

//overlapping lines
%overlapping_lines {
	overflow: hidden;

	&:hover {
		&::before {
			background-color: #fff;
			right: 50%;
			left: 50%;
			opacity: 0.02;
		}

		&::after {
			background-color: #fff;
			top: 50%;
			bottom: 50%;
			opacity: 0.02;
		}
	}

	&::before,
	&::after {
		content: "";
		position: absolute;
		right: 0;
		left: 0;
		top: 0;
		bottom: 0;
		transition: all 0.5s ease;
	}
}

//form group
%form_group_global {
	margin-bottom: 20px;

	label {
		display: block;
		margin-bottom: 15px;
		font-weight: 600;
		text-transform: capitalize;
		font-size: 14px;
	}

	.form-control,
	.form-select {
		display: block;
		width: 100%;
		padding: 15px 0 10px;
		color: #222222;
		font-size: 16px;
		background-color: transparent;
		border: none;
		border-bottom: 1px solid $dark_color;
		border-radius: 0;

		&:focus {
			border-color: $main_color;
			box-shadow: none;
		}

		&.is-invalid {
			border-color: #dc3545;
		}

		&::placeholder {
			font-weight: 400;
			text-transform: capitalize;
		}
	}

	textarea {
		height: auto !important;
	}
}

//placeholder selectors

//global styles
.btn,
.btn-close {
	&:focus {
		box-shadow: none;
		outline: none;
	}
}

dl,
ol,
ul {
	padding: 0;
	padding-inline-start: 20px;
}

.modal-backdrop.show {
	opacity: .7;
}

a {
	text-decoration: none;
}

::-moz-selection {
	color: #fff;
	background: $dark_color;
}

::selection {
	color: #fff;
	background: $dark_color;
}

//global styles

//main title
.main-title {
	margin-bottom: 2rem;
	position: relative;

	h2 {
		font-size: 2em;
		position: relative;
		margin-bottom: 0;
		padding-bottom: 15px;
		text-transform: uppercase;
	}
}

//main title

//to top button
#toTop {
	position: fixed;
	right: 15px;
	bottom: 15px;
	margin: 0;
	z-index: 99;
	display: none;

	button {
		background-color: $main_color;
		width: 40px;
		height: 40px;
		border: none;
		color: #fff;
		border-radius: 5px;
	}
}

//to top button

//preloader
.preloader {
	position: fixed;
	z-index: 999;
	top: 0;
	height: 100%;
	width: 100%;
	background-color: $main_color;
	display: flex;
	align-items: center;
	justify-content: center;

	img {
		max-width: 250px;
	}
}
//preloader